.wrapper {
  min-height: 100vh;
  overflow: hidden;

  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
  background-color: #153c6b;
  padding: 1.2rem 0;
  display: flex;
}

.container {
  max-width: 117rem;
  margin: 0 auto;
  padding: 0 1.6rem;
}
